document.addEventListener('DOMContentLoaded', function () {
    crearGaleria();
});

function crearGaleria() {
    const galeria = document.querySelector('.galeria__img');
    
    for (let i = 1; i <= 12; i++) {

        const img = document.createElement("IMG");
        img.src = `./build/img/thumb/${i}.webp`;
        img.onclick = mostrarImg;   // funcion con nombre
        // aÃ±adir atributo, crear atributos personalizados en html5
        img.dataset.imagenId = i;

        const lista = document.createElement("LI");
        lista.appendChild(img);
        galeria.appendChild(lista);
    }
}

function mostrarImg(e) {
    /* console.log(e);
    console.log(e.target.dataset.imagenId); */

    const id = +(e.target.dataset.imagenId);
    /* console.log(id)
    console.log(typeof (id)); */

    const img = document.createElement("IMG");
    img.src = `./build/img/grande/${id}.webp`;
    /* console.log(img); */

    const overlay = document.createElement("DIV");
    overlay.appendChild(img);
    overlay.classList = "overlay";

    // cuando se da click cerrar img
    overlay.onclick = function() {
        overlay.remove();
        body.classList.remove("fijar-body");
    };

    // btn para cerrar img
    const btn_cerrar = document.createElement("P");
    btn_cerrar.textContent = "X";
    btn_cerrar.classList.add("btn-cerrar");

    // cuando se presiona cerrar img
    btn_cerrar.onclick = function () {  // funcion anonima
        overlay.remove();
        body.classList.remove("fijar-body");
    };

    overlay.appendChild(btn_cerrar);

    //mostrar en el html
    const body = document.querySelector("body");
    body.appendChild(overlay);
    // fijar, que no se pueda hacer scroll cuando se abre
    body.classList.add("fijar-body");

}

